    background:rgba(41,41,41,1);
    color: #FFFFFF;
    display:block;
    .center-block{
      div{
        height:100%;
        position:relative;
        float:left;
      }
      i, span{
        position:relative;
        float:left;
      }
      .logo{
        margin-top: 10px;
